<template>
    <details :open="is_open">
        <summary class="smaller"> {{ item.title }} </summary>
        <div id="v-model-multiple-checkboxes">
            <div v-for="filter in Object.keys(item.options)" :key="filter">
                <input
                    type="checkbox"
                    :id="item.key"
                    :value="filter"
                    @click="toParent(filter)"
                    class="small"
                />
                <label :for="item.options[filter]" class="smaller">
                    &nbsp;{{ filter }} ({{ item.options[filter] }})
                </label>
            </div>
        </div>
    </details>
</template>

<script>
export default {
    props: ["item", "is_open"],
    emits: ["filter"],

    name: "FilterItem",

    data() {
        return {
            title: "",
            filters: [],
            new_filter: "",
        };
    },

    methods: {
        toParent(data) {
            // console.log("item = ", this.item);
            console.log("toParent! ", event.target.checked);
            // console.log("key = ", this.item.key);

            this.$emit("filter", {
                key: this.item.key,
                value: data,
                is_checked: event.target.checked,
            });
        },
    },
};
</script>

